@import '_variables';
@import '_mixins';
@import '~video.js/dist/video-js.css';
@import '~videojs-dock/dist/videojs-dock.css';

$assets-path: '../../assets/';
@import '../../sass/player/index';


video {
  width: 100%;
}

#video-wrapper {
  width: 100%;
  height: 100%;
}

.video-js .vjs-tech{
    position: relative;
    left: 0;
    top : 0;
    bottom: auto;
    right: auto;
    height: auto;
}

.video-js{
    height: auto!important;

    video{
      max-height: 640px;
    }

    &.vjs-fullscreen{
      video{
        max-height: 100%;
      }
    }
}

.vjs-peertube{
    display: none!important;
}

.video-js {
  width: 100%;
  overflow: hidden;

  .vjs-poster {
    background-size: 100% auto;
  }

  @media screen and (max-width: 350px) {
    .vjs-play-control {
      padding: 0 5px !important;
      width: 25px !important;
    }

    .vjs-volume-control {
      display: none !important;
    }

    .vjs-volume-panel {
      width: 26px !important;
    }

    .vjs-peertube-link {
      padding: 0;
    }
  }
}

#placeholder-preview {
  position: absolute;
  top: 0;
  left: 0;
  background-size: 100% auto;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
}





