.pocketnet-logo-video-player {
    max-width: 100%;
}

.js-player{

    .vjs-menu *{
        font-family: 'Segoe UI', SegoeUI, "Helvetica Neue", Helvetica, Arial, sans-serif!important;
    }

    .vjs-menu-content{
        overflow-y: hidden;
    }

    .video-js .vjs-time-tooltip{top: -3.9em;}.video-js .vjs-control:focus, .video-js .vjs-control:focus:before, .video-js .vjs-control:hover:before{text-shadow: 0 0 0em #fff;}.vjs-menu li.vjs-menu-item:focus,.vjs-menu li.vjs-menu-item:hover{outline: none}.video-js.vjs-fullscreen{}.video-js button{outline: none}.video-js .vjs-controls-disabled .vjs-big-play-button{display: none!important}.vjs-paused.vjs-has-started.video-js .vjs-big-play-button,.video-js.vjs-ended .vjs-big-play-button,.video-js.vjs-paused .vjs-big-play-button{display: block}.video-js .vjs-load-progress div,.vjs-seeking .vjs-big-play-button,.vjs-waiting .vjs-big-play-button {display: none!important}.video-js.vjs-ended .vjs-big-play-button{display: block!important}.video-js .vjs-big-play-button{top: 50%;left: 50%;margin-left: -1em;margin-top: -1em;width: 2em;height: 2em;line-height: 2em;border: none;border-radius: 50%;font-size: 3.5em;background-color: rgba(0, 0, 0, .45);color: #fff;-webkit-transition: border-color .4s, outline .4s, background-color .4s;-moz-transition: border-color .4s, outline .4s, background-color .4s;-ms-transition: border-color .4s, outline .4s, background-color .4s;-o-transition:border-color .4s, outline .4s, background-color .4s;transition: border-color .4s, outline .4s, background-color .4s}.video-js .vjs-big-play-button{background-color: rgba(0, 0, 0, 0);font-size: 12em;border-radius: 0%;height: 1em!important;line-height: 1em!important;margin-top: -0.5em!important}.video-js:hover .vjs-big-play-button,.video-js .vjs-big-play-button:focus,.video-js .vjs-big-play-button:active{background-color: rgba(0, 0, 0, 0)}.video-js .vjs-remaining-time-display{display: none}.video-js .vjs-duration{display: block;left: 0em}.video-js .vjs-current-time{display: block;left: 0}.vjs-live .vjs-time-control{display: none}

    .vjs-time-divider{
        display: block;
    }

    .pocketnet-logo-video-player .icon.icon-full-logo-transparent {
      background: url('#{$assets-path}/player/images/logo.svg') no-repeat;
      width: 100px;
      height: 15px;
      display: block;
      opacity: 0.5;
    }

    .pocketnet-logo-video-player {
      max-width: 100%;
      left : 0.5em;
      top : 0.5em;
      position: absolute;
    }

}


@media screen and (max-width: 300px) {
  #error-block {
    font-size: 36px;

    #error-content {
      font-size: 14px;
    }
  }
}

